import React, { Component } from "react";

import "./HomePage.css";

import { Link } from "react-router-dom";

import Adsense from "../../components/Adsense/Adsense.js";
import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";
import HashTagSet from "../../components/HashTags/HashTagSet/HashTagSet.js";
import Button from "../../components/Buttons/index.js";
import Modal from "../../components/Modal/Modal.js";
import {Important, Comment, Line} from "../../components/TextUtils/TextUtils.js";

import GetOperatingSystem from "../../utils/os.js";

import logoImage from '../../img/logo.png';


export default class HomePage extends Component {
  constructor() {
    super();
    this.state = {
      macDownloadModal: false
    };
    this.macDownloadUrl = "https://github.com/Leatherback-Azi/TwitchLink-MacOS";
    this.showMacDownloadModal = (show) => {
      this.setState({macDownloadModal: show});
    }
    this.downloadMac = () => {
      this.showMacDownloadModal(false);
      window.open(this.macDownloadUrl, "_blank");
    }
    this.closeMacDownload = () => {
      this.showMacDownloadModal(false);
    }
  }

  latestVersion = "1.0.4";

  T(en, ko) {
    if (this.props.language == "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  getDownloadUrl() {
    var newUrl = new URL(window.location.href);
    newUrl.pathname = "/donate/";
    newUrl.searchParams.set("download", this.latestVersion);
    return newUrl.pathname + newUrl.search;
  }

  render() {
    return (
      <div className="HomePage">
        <PageFrame>
          <Content.TitleBar>TwitchLink</Content.TitleBar>
          <Content.Area className="downloadLatestVersion" style={{padding: "30px"}}>
            <ImageLabel style={{padding: "15px", width: "200px", height: "200px"}} src={logoImage}/>
            <div className="infoArea" style={{marginLeft: "40px", width: "100%"}}>
              <div style={{display: "flex"}}>
                <Label style={{fontSize: "35pt"}}>TwitchLink</Label>
                <Label style={{marginTop: "30px", fontSize: "15pt", color: "gray"}}>{this.latestVersion}</Label>
              </div>
              <Label style={{fontSize: "15pt", color: "gray"}}>{this.T("Twitch Stream/Video/Clip Downloader", "트위치 생방송/비디오/클립 다운로더")}</Label>
              <Button.ButtonArea>
                <Button.LinkButton href="https://github.com/devhotteok/TwitchLink" target="_blank">{this.T("Source Code", "소스코드")}</Button.LinkButton>
                <Link className="Button downloadButton" to={this.getDownloadUrl()}>{this.T("Download", "다운로드")}</Link>
                {GetOperatingSystem() == "mac" ? <Link className="Button downloadButton caution" onClick={() => this.showMacDownloadModal(true)}>{this.T("Download for Mac", "Mac 버전 다운로드")}</Link>:null}
              </Button.ButtonArea>
            </div>
          </Content.Area>
          <Adsense
            name="TwitchLink_Home_1"
            style={{"display": "block"}}
            dataAdSlot="7190249483"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Content.Area>
            <Label style={{margin: "0 0 30px 10px", fontSize: "20pt"}}>{this.T("Features", "특징")}</Label>
            <Content.Box>
              <Content.TextArea className="features">
                - {this.T("Download Streams, Videos, Clips", "생방송, 비디오, 클립 다운로드")}
                <br/>
                - {this.T("Download subscribers-only Videos", "구독자 전용 비디오 다운로드")}
                <br/>
                - {this.T("Unmute Videos", "비디오 음소거 해제")}
                <br/>
                - {this.T("Super Fast Download", "초고속 다운로드")}
                <br/>
                - {this.T("Time Selection For Video Downloads", "비디오 부분 다운로드")}
                <br/>
                - {this.T("Channel Bookmarks", "채널 북마크")}
                <br/>
                - {this.T("Filename Templates", "파일명 템플릿")}
                <br/>
                - {this.T("Language/Time Localization", "언어/시간 현지화")}
                <br/>
                - {this.T("Free & Open Source", "무료 & 오픈소스")}
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
          <Adsense
            name="TwitchLink_Home_2"
            style={{"display": "block"}}
            dataAdSlot="3251004473"
            dataAdFormat="auto"
            dataFullWidthResponsive="true"
          />
          <Content.Area className="faq">
            <Label style={{margin: "0 0 30px 10px", fontSize: "20pt"}}>{this.T("FAQ", "자주 묻는 질문")}</Label>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "Recently, more and more streamers are not saving replays due to DMCA regulations. What should I do?",
                  "최근 DMCA 규제로 다시보기를 남기지 않는 스트리머가 늘어나고 있습니다. 이런 경우는 어떻게 하나요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try TwitchLink's live download feature! Live broadcasts without replays can be downloaded in real time!",
                  "TwitchLink의 실시간 다운로드 기능을 사용해보세요! 다시보기가 남지 않는 생방송도 실시간으로 다운로드할 수 있습니다!"
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "Can TwitchLink download subscribers-only videos?",
                  "TwitchLink는 구독자 전용 영상을 다운로드할 수 있나요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Of course! TwitchLink can also download subscriber only videos.",
                  "TwitchLink는 구독자 전용 영상도 다운로드할 수 있습니다."
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "My video has been muted! Is there any way to recover?",
                  "동영상이 음소거되었습니다. 복구할 방법이 있을까요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try TwitchLink's unmute feature! It recovers the original file as much as possible!",
                  "TwitchLink의 음소거 해제 기능을 사용해보세요! 가능한 한 원본 파일을 찾아 복구합니다!"
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "Some sounds are missing in replays due to soundtrack settings. What should I do?",
                  "트랙 분리로 인해 다시보기에서 일부 소리가 나지 않습니다. 이런 경우는 어떻게 하나요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try TwitchLink's live download feature! You can save all sounds regardless of the soundtrack settings.",
                  "TwitchLink의 실시간 다운로드 기능을 사용해보세요! 트랙 분리가 되는 생방송의 소리도 모두 다운로드할 수 있습니다!"
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "A lot of clips were deleted due to recent DMCA regulations. I want to keep the clips before this happens again.",
                  "최근 DMCA 규제로 수많은 클립들이 사라졌습니다. 이런 일이 또 발생하기 전에 클립들을 보관하고 싶습니다."
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try TwitchLink's Clip Download feature! You can easily manage various data such as clip name, date, channel, and creator.",
                  "TwitchLink의 클립 다운로드 기능을 사용해보세요! 클립의 이름, 날짜, 채널, 제작자 등 다양한 데이터를 손쉽게 관리하고 다운로드할 수 있습니다."
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "It's too slow to download large videos. Is there a solution?",
                  "용량이 큰 동영상을 다운로드하는 것이 너무 느립니다. 해결책이 있나요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try the super-fast download feature of TwitchLink!",
                  "TwitchLink의 초고속 다운로드 기능을 사용해보세요! 차원이 다른 최고의 속도를 보장합니다."
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.Subtitle>
                {this.T(
                  "It's annoying to search for channels every time. Is there any other good way?",
                  "매번 채널을 검색하기 귀찮습니다. 다른 좋은 방법이 있을까요?"
                )}
              </Content.Subtitle>
              <Content.TextArea>
                {this.T(
                  "Try TwitchLink's bookmarks feature! You can register your favorite channels and search easily!",
                  "TwitchLink는 채널 북마크 기능을 제공합니다. 자주 찾는 채널을 등록하고 편하게 검색하세요!"
                )}
              </Content.TextArea>
            </Content.Box>
            <Content.Box>
              <Content.TextArea>
                <HashTagSet>
                  {this.T(
                    "#TwitchLink #Twitch Downloader #Stream Download #Stream Record #Video Download #Clip Download #Download Subscribers-Only Video #Unmute Video",
                    "#트위치링크 #트위치 다운로더 #생방송 실시간 다운로드 #생방송 녹화 #다시보기 다운로드 #클립 다운로드 #구독자 전용 다시보기 다운로드 #음소거 해제"
                  )}
                </HashTagSet>
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
          <Modal showModal={this.state.macDownloadModal} modalTitle={this.T("Warning", "경고")} okButton={<Important>{this.T("Download", "다운로드")}</Important>} cancelButton={this.T("Cancel", "취소")} okCallback={this.downloadMac} cancelCallback={this.closeMacDownload}>
            <Label>
              <Important>
                {this.T(
                  "This content is not the official TwitchLink software. The TwitchLink developer does not guarantee the security or reliability of this content.",
                  "이 콘텐츠는 TwitchLink 공식 소프트웨어가 아니며 TwitchLink 개발자는 이 콘텐츠의 보안성, 안정성에 대해 보증하지 않습니다."
                )}
              </Important>
            </Label>
            <Line/>
            <Label>
              {this.T(
                "This content has been modified by a third party and has not been reviewed by the TwitchLink developer.\nDo you still want to download it?",
                "이 콘텐츠는 제3자에 의해 수정되었으며, TwitchLink 개발자에 의해 검토되지 않았습니다.\n그래도 다운로드하시겠습니까?"
              )}
            </Label>
            <Label>
              <Comment>
                {this.T(
                  "This link is only valid until TwitchLink officially supports the Mac version.",
                  "이 링크는 TwitchLink에서 공식적으로 Mac 버전을 지원하기 전까지만 유효합니다."
                )}
              </Comment>
            </Label>
          </Modal>
        </PageFrame>
      </div>
    );
  }
}