import React from "react";


export function TextGroup(props) {
    return (
        <p>
            {props.children}
        </p>
    );
};


export function Important(props) {
    return (
        <span style={{color: "red", fontWeight: "bold"}}>
            {props.children}
        </span>
    );
};

export function Comment(props) {
    return (
        <span style={{color: "gray"}}>
            {props.children}
        </span>
    );
};

export function Title(props) {
    return (
        <div style={{marginBottom: "5px", fontSize: "25px", fontWeight: "bold"}}>
            {props.children}
        </div>
    );
}

export function SubTitle(props) {
    return (
        <div style={{marginBottom: "3px", fontSize: "20px", fontWeight: "bold"}}>
            {props.children}
        </div>
    );
}

export function Line() {
    return <hr style={{margin: "15px 0"}}/>
}